@import "colors.scss";

footer {
  background-color: $first;
  width: 100%;;
  height: 200px;
  color: $dark;
  padding: 2rem;
  display: grid;
  grid-template-columns: 4fr 1fr;

  > div {
    display: flex;
    flex-direction: column;
    justify-content: center;

    > h1 {
      font: 700 2rem $text1;
      text-transform: uppercase;
      color: $third;
    }

    p {
      font: 500 1.2rem $text1;
      margin-top: 5px;
    }

    > h5 {
      font: 300 2rem $text1;
      margin: 1rem 0;
      text-align: center;
    }

    > div {
      display: flex;
      flex-direction: column;
      align-items: center;

      > a {
        color: $dark;
        font: 900 1rem $text1;
        margin-bottom: 5px;
        &:hover {
          color: $third;
        }
      }
    }
  }
}

